.App {
  text-align: center;
  font-family:monospace;
  margin:0;
  height: 100%;
  scroll-behavior: smooth;
}

header {
  background-color:white;
  height: 100%;
  }

.Profile-Main-Logo{
  height:500px;
  padding-top: 20px;
}

.Profile-Name{
  font-size: 25px;
  font-weight: bolder;
  -webkit-animation-name: color-change; /* Safari 4.0 - 8.0 */
    -webkit-animation-duration: 2s; /* Safari 4.0 - 8.0 */
    animation-name: color-change;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-delay:2s;
            animation-delay:2s;
}

@-webkit-keyframes color-change {
   0%   {color: Black;}
   25%  {color: DarkSlateGray;}
   50%  {color: DimGrey;}
   100% {color: Gray;}
}

@keyframes color-change {
   0%   {color: Black;}
   25%  {color: DarkSlateGray;}
   50%  {color: DimGrey;}
   100% {color: Gray;}
}

.App-logo {
  height: 200px;
  float:left;
  padding: 0 15px 15px 10px;
}

.About-Me-Content{
  font-size: 15px;
  text-align: justify;
  padding-right: 10px;
}

.App-intro {
  font-size:30px;
  color: DarkSlateGray;
  font-weight: bolder;
  background-color:#D0D3D4;
  height: 200px;
  padding-top: 10%;
}

.Section{
padding-bottom: 50px;
background-color: #ECF0F1;
width: 90%;
border-spacing: 20px;
display:inline-block;
}

.App-side{
  font-weight: bolder;
  color:DarkSlateGray;
  font-size: 35px;
  padding-top: 40px;
  width:50%;
}

.App-side-content{
  color: black;
  font-size: 15px;
  text-align: justify;
  display: inline-block;
  width:50%;
  padding-top: 40px;

  }

.Contact-info{
  font-size: 15px;
  background-color: #ECF0F1;
}

.Contact-options{
  padding-top: 20px;
  }

a:link, a:visited, a:active {
  color:DarkSlateGray;
  text-decoration: none;
  text-align: center;
  font-weight: bolder;
  font-size:15px;
  padding-right: 10px;
}

a:hover{
  color: #979A9A;
  text-decoration: none;
  text-align: center;
}

li{
  list-style: disc;
}

i{
    display: inline-block;
}

